.contact {
  height: auto;
  width: 100%;
  padding: 0 10px 30px; }

.contactTitle {
  display: block;
  position: relative;
  font-family: Quicksand, sans-serif;
  line-height: 20px;
  text-align: center;
  margin: 50px 0 70px 0;
  font-size: 36px;
  font-weight: 500;
  color: #222222; }
  .contactTitle:after {
    position: absolute;
    content: '';
    background: url("./blackLine.png") no-repeat;
    width: 100%;
    height: 10px;
    top: 35px;
    left: 0;
    right: 0;
    bottom: 0;
    background-position: center bottom; }

.blockContactInfo h2 {
  color: black;
  font-size: 20px; }

.rowContactInfo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex: 1 1 300px;
      flex: 1 1 300px;
  -ms-flex-pack: distribute;
      justify-content: space-around; }

.blockForm {
  width: 100%;
  max-width: 500px; }

.blockContactInfo {
  width: 100%;
  max-width: 500px;
  margin: 0 15px 0 5px; }

input {
  width: 100%;
  margin: 5px 10px 5px 0;
  padding: 15px 0 15px 5px;
  font-size: 18px;
  border: none;
  max-width: 550px;
  border-radius: 10px;
  outline: none;
  font-family: Quicksand, sans-serif; }

textarea {
  width: 100%;
  max-width: 550px;
  margin: 5px 10px 5px 0;
  padding: 15px 0 15px 5px;
  resize: none;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  font-family: Quicksand, sans-serif;
  outline: none; }

.sendMessageBtn {
  margin-right: 30px;
  background-color: #fc7d7d;
  color: white;
  padding: 10px 40px;
  margin-top: 30px;
  border-radius: 10px;
  border: 2px solid #fc7d7d;
  display: inline-block;
  font-family: 'Droid Sans', sans-serif;
  font-size: 18px;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.borderButton {
  color: white;
  padding: 10px 40px;
  margin-top: 30px;
  border: 2px solid #fc7d7d;
  border-radius: 10px;
  display: inline-block;
  font-family: 'Droid Sans', sans-serif;
  font-size: 18px;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.sendMessageBtn:hover {
  text-decoration: none;
  color: white;
  background-color: transparent; }

.borderButton:hover {
  text-decoration: none;
  color: white;
  background-color: #fc7d7d; }

.arrow {
  display: -ms-flexbox;
  display: flex;
  margin-top: 25px; }

img.arrowImg {
  position: relative;
  top: 5px;
  left: 10px; }

.contactBlock {
  margin: 0; }

.contactBlock img {
  padding: 0 10px 0 0; }

@media (max-width: 550px) {
  .arrowImg {
    display: none; }
  .blockForm {
    width: 85%; } }



